import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Box from "@material-ui/core/Box";
import Footer from "../components/Footer";
import AppBar from "../components/AppBar";

import VimeoPlayer from "react-player/lib/players/Vimeo";
import MosProjektFinal from "../images/MOS.jpg";
import FaceProjektFinal from "../images/FACE_RECOG.jpg";
import mountain01 from "../images/mountain01.png";
import cave01 from "../images/cave01.png";
import cave02 from "../images/cave02.png";
import CLList from "../images/CLList.png";

import Chip from "@material-ui/core/Chip";

import Raytracer from "../images/MCRT.jpg";

import ABOBReport from "../documents/Report_Abob.pdf";
import MCReport from "../documents/Report_TNCG15.pdf";
import KANReport from "../documents/Report_TNM094.pdf";
import MOSReport from "../documents/Report_Mos.pdf";
import PROCReport from "../documents/Report_TNM084.pdf";
import CLOUDReport from "../documents/Report_CLOUDMATCH.pdf";

import { OutboundLink } from "gatsby-plugin-google-analytics";

const undefinedTitel = (location) => {
  if (location.state === null || location.state === undefined) {
    return <>Monte Carlo Ray Tracer</>;
  } else {
    return <>{location.state.title}</>;
  }
};

const featureListProc = [
  "Batching",
  "Face culling",
  "Marching cubes algorithm",
  "Simplex noise",
  "Infinite generation",
  "Exponential fog",
  "Camera",
  "Directional light",
  "Cave systems",
  "Mountainlike terrain",
  "ImGui",
];

const featureListCloud = ["KNN", "Data gathering", "Scoring algorithm"];

const featureListFace = [
  "Morphology",
  "Eigenfaces",
  "Fisherfaces",
  "PCA",
  "LDA",
  "Face masking",
  "Eyemap",
  "Mouthmap",
  "Face normalization",
  "Face alignment",
  "Light compensation",
];

const featureListMC = [
  "Monte Carlo Estimator",
  "Russian Roulette",
  "Lambertian Reflectors",
  "Oren-Nayar Reflectors",
  "Pure Reflective Surfaces",
  "Transparent Objects",
  "Multi Sampling",
  "Anti Aliasing",
  "Snell's law",
  "Brewster's law",
  "Color bleeding",
];

const featureListMetiri = [
  "Real Time Measure",
  "Store Measurment Data",
  "Surface Detection",
  "Surface Visualization",
  "Onboarding Introduction",
];

const featureListMos = [
  "Drag",
  "Gravity",
  "Bounce",
  "Collision",
  "Magnus Effect",
  "GUI",
  "Object Import",
];

const featureListAmaze = [
  "VR",
  "Animations",
  "Puzzles",
  "Interactive objects",
  "Trigger effects",
];

const featureListBibliu = [
  "Posting",
  "Searching",
  "Notifications",
  "Autofill queries",
  "Wishlist",
  "User verification",
];

const techListProc = ["C++", "OpenGL", "GLSL"];
const techListCloud = [
  "Python",
  "JavaScript",
  "React",
  "Flask",
  "Spotify API",
  "Soundcloud API",
];
const techListFace = ["MATLAB"];
const techListMC = ["C++", "OpenMP"];
const techListMetiri = ["ARCore", "OpenGL ES", "Android", "Java", "Room"];
const techListMos = ["C++", "OpenGL", "MATLAB"];
const techListAmaze = ["Unreal Engine", "Samsung Gear VR", "3ds Max"];
const techListBibliu = ["JavaScript", "React Native", "Firebase"];

const getContent = (location) => {
  if (
    location.state === null ||
    location.state === undefined ||
    location.state.title === "Monte Carlo Ray Tracer"
  ) {
    return (
      <>
        <Typography color="primary" paragraph>
          A Monte Carlo Ray tracer made during the course Advanced Global
          Illumination and Rendering at Linköpings University. The ray tracer is
          physically based and were implemented in C++. The underlying physics
          range from Bidirectional Reflectance Distribution Functions to
          Estimations of the light contribution from the rendering equation.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListMC.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListMC.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          The{" "}
          <OutboundLink
            style={{
              color: "#F20544",
              textDecoration: "inherit",
            }}
            size="small"
            rel="noopener noreferrer"
            target="_blank"
            href={MCReport}
          >
            report
          </OutboundLink>{" "}
          contains more images and performance analysis for different
          parameters.
        </Typography>

        <img
          style={{
            display: "block",
            width: "100%",
            marginLeft: "auto",
            border: "1px solid #010A26",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={Raytracer}
          alt="Raytracer"
        />
      </>
    );
  } else if (location.state.title === "Metiri") {
    return (
      <>
        <Typography color="primary" paragraph>
          The Android application Metiri was developed during the course TNM094,
          Media Technology - Bachelor Project. Metiri is the Latin word for
          "measured" and describes the purpose of the product. The application
          is a mobile measurement tool that uses augmented reality to measure
          flat surfaces in real time. It is also possible to measure flat
          surfaces in stored environments. Metiri has been developed using
          Google's platform ARCore, the 3D API OpenGL ES and the database
          library Room. The project was developed using the agile methodology
          Scrum.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListMetiri.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListMetiri.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>

        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          This video showcase the main features of the application. The{" "}
          <OutboundLink
            style={{
              color: "#F20544",
              textDecoration: "inherit",
            }}
            size="small"
            rel="noopener noreferrer"
            target="_blank"
            href={KANReport}
          >
            report (swedish)
          </OutboundLink>{" "}
          contains more images and an in-depth description of the project.
        </Typography>

        <div
          style={{
            position: "relative",
            paddingTop: "56.25%",
            marginBottom: "40px",
          }}
        >
          <VimeoPlayer
            url="https://vimeo.com/369190884"
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              border: "1px solid #010A26",
            }}
            width="100%"
            height="100%"
          />
        </div>
      </>
    );
  } else if (location.state.title === "Football Simulator") {
    return (
      <>
        <Typography color="primary" paragraph>
          This project was developed during the course TNM085, Modelling
          Project. The goal was to create a mathematical model that describes a
          projectile's trajectory. The model was verified using simulation tools
          and finally visualized in a three-dimensional computing environment.
          The result is a physically realistic football simulator where the user
          can set physical parameters and see how the trajectory differs.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListMos.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListMos.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          The{" "}
          <OutboundLink
            style={{
              color: "#F20544",
              textDecoration: "inherit",
            }}
            size="small"
            rel="noopener noreferrer"
            target="_blank"
            href={MOSReport}
          >
            report (swedish)
          </OutboundLink>{" "}
          contains more images and a description of the math and physics used.
        </Typography>

        <img
          style={{
            display: "block",
            width: "100%",
            border: "1px solid #010A26",
            marginLeft: "auto",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={MosProjektFinal}
          alt="Football Simulator"
        />
      </>
    );
  } else if (location.state.title === "Bibliuteket") {
    return (
      <>
        <Typography color="primary" paragraph>
          Bibliuteket is a mobile application to help students at Linköpings
          University to sell and buy used course literature. The user can post
          books for sale as well as search for currently available books to buy.
          The user can also add books to a wishlist to recieve notifications as
          soon as the books are available. An integrated Facebook Messenger
          button is used to handle the contact between users. Bibliuteket is
          availible for download at Google Play and App store.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListBibliu.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListBibliu.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          This video showcase the main features of the game.
        </Typography>

        <div
          style={{
            position: "relative",
            paddingTop: "56.25%",
            marginBottom: "40px",
          }}
        >
          <VimeoPlayer
            url="https://vimeo.com/386549456"
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              border: "1px solid #010A26",
            }}
            width="100%"
            height="100%"
          />
        </div>
      </>
    );
  } else if (location.state.title === "Amaze") {
    return (
      <>
        <Typography color="primary" paragraph>
          Amaze is a Virtual reality game developed for Samsung Gear VR. The
          game was implemented using Unreal Engine combined with 3Ds Max.
          Developing a VR Game for a phone comes with some constraints regarding
          the performance. However, by using well-thought-out techniques and
          tricks the game still deliver a relative realistic experience.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListAmaze.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListAmaze.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          This video showcase the main features of the game.
        </Typography>

        <div
          style={{
            position: "relative",
            paddingTop: "56.25%",
            marginBottom: "40px",
          }}
        >
          <VimeoPlayer
            url="https://vimeo.com/272102306"
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              border: "1px solid #010A26",
            }}
            width="100%"
            height="100%"
          />
        </div>
      </>
    );
  } else if (location.state.title === "Face Recognition Software") {
    return (
      <>
        <Typography color="primary" paragraph>
          A Face Recognition program which takes in an image of a face and
          returns an identification number of a matching face from a database.
          The program consist of a pipeline which includes: light compensation,
          normalization, eye and mouth map extraction and lastly feature
          extraction for each image. These features are what allows the program
          to determine if there is a match.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListFace.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListFace.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          The{" "}
          <OutboundLink
            style={{
              color: "#F20544",
              textDecoration: "inherit",
            }}
            size="small"
            rel="noopener noreferrer"
            target="_blank"
            href={ABOBReport}
          >
            report
          </OutboundLink>{" "}
          contains more images and an in-depth description of the project and
          its accuracy.
        </Typography>

        <img
          style={{
            display: "block",
            width: "100%",
            border: "1px solid #010A26",
            marginLeft: "auto",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={FaceProjektFinal}
          alt="Face recog"
        />
      </>
    );
  } else if (location.state.title === "Cloudmatch") {
    return (
      <>
        <Typography color="primary" paragraph>
          Tool to connect Spotify and Soundcloud users to recommend amateur
          music for the Spotify users. Data from 30 000 Soundcloud users with a
          total of 24 000 000 tracks were collected. The Spotify user is then
          connected to a group of Soundcloud users based on their mean favorite
          genre. From that group of people, tracks are choosen according to a
          scoring system.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListCloud.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListCloud.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          The application is unfortunately not hosted on a webserver. However,
          if you want to try it, you can contact me and I will give you access
          to the data and help you run it locally. More details about the
          project can be found in the{" "}
          <OutboundLink
            style={{
              color: "#F20544",
              textDecoration: "inherit",
            }}
            size="small"
            rel="noopener noreferrer"
            target="_blank"
            href={CLOUDReport}
          >
            report.
          </OutboundLink>
        </Typography>

        <img
          style={{
            display: "block",
            width: "100%",
            border: "1px solid #010A26",
            marginLeft: "auto",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={CLList}
          alt="Music"
        />
      </>
    );
  } else if (location.state.title === "Proccraft") {
    return (
      <>
        <Typography color="primary" paragraph>
          This is a voxel terrain engine implemented from scratch in C++ with
          the graphics API OpenGL and the shading language GLSL. The engine uses
          a combination of the marching cubes algorithm and simplex noise to
          generate the voxels and the materials. The genereated terrains are
          infinite and highly customizeable. For mountainlike terrains,
          2D-simplex noise were used to generate a heightmap. For cave systems
          3D-simplex noise were used to enable overhangs.
        </Typography>
        <Typography variant="h6" color="primary">
          Features
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {featureListProc.map((feature) => (
            <Chip size="small" label={feature} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Technologies
        </Typography>
        <div
          style={{
            display: "flex",
            justifyContent: "left",
            flexWrap: "wrap",
            marginBottom: "20px",
          }}
        >
          {techListProc.map((tech) => (
            <Chip size="small" label={tech} style={{ margin: "5px" }} />
          ))}
        </div>
        <Typography variant="h6" color="primary">
          Result
        </Typography>
        <Typography color="primary" paragraph>
          The{" "}
          <OutboundLink
            style={{
              color: "#F20544",
              textDecoration: "inherit",
            }}
            size="small"
            rel="noopener noreferrer"
            target="_blank"
            href={PROCReport}
          >
            report
          </OutboundLink>{" "}
          contains more images and an in-depth description of the project and
          its performance.
        </Typography>

        <img
          style={{
            display: "block",
            width: "100%",
            border: "1px solid #010A26",
            marginLeft: "auto",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={cave01}
          alt="cave01"
        />
        <img
          style={{
            display: "block",
            width: "100%",
            border: "1px solid #010A26",
            marginLeft: "auto",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={cave02}
          alt="cave02"
        />
        <img
          style={{
            display: "block",
            width: "100%",
            border: "1px solid #010A26",
            marginLeft: "auto",
            marginRight: "auto",
            marginBottom: "40px",
          }}
          src={mountain01}
          alt="mountain01"
        />
      </>
    );
  }
};

const Project = ({ location }) => {
  return (
    <React.Fragment>
      <CssBaseline />

      <main>
        <AppBar />
        <Container maxWidth="md">
          <Box my={4}>
            <Typography
              component="h4"
              variant="h4"
              gutterBottom
              color="primary"
            >
              {undefinedTitel(location)}
            </Typography>
            {getContent(location)}
          </Box>
        </Container>
        <Footer />
      </main>
    </React.Fragment>
  );
};

export default Project;
